import React, { useEffect, useState } from 'react';
import googleSearchLogo from '../images/googleSearchLogo.svg';
import { FaSistrix, FaMicrophone } from "react-icons/fa";
import { key, cx } from '../Api';
import axios from 'axios';
import Show from './Show';

const Search = (props) => {
    const goBack = () => {
        props.history.push("/");
    };
    const [state, setstate] = useState(props.location.state ? props.location.state : "");
    const [results, setResults] = useState([]);
    const [info, setInfo] = useState('');
    const searchGoogle = async (e) => {
        e.preventDefault();
        try {
            const responce = await axios.get(`https://www.googleapis.com/customsearch/v1?key=${key}&cx=${cx}&q=${state}`);
            if (responce) {
                console.log(responce.data.items, responce.data.searchInformation);
                setResults(responce.data.items);
                setInfo(responce.data.searchInformation);
            }
        } catch (error) {
            console.log(error);
        }
    };

    useEffect(() => {
        const getPosts = async () => {
            if (props.location.state) {
                try {
                    const response = await axios.get(
                        `https://www.googleapis.com/customsearch/v1?key=${key}&cx=${cx}&q=${state}`
                    );
                    if (response) {
                        console.log(response);
                        setResults(response.data.items);
                        setInfo(response.data.searchInformation);
                    }
                } catch (error) {
                    console.log(error);
                    console.log(error.message);                 
                }
            }
        };
        getPosts();
    }, []);

    return (
        <>
            <div className="search">
                <div className="search__form">
                    <div className="search__form-logo">
                        <img src={googleSearchLogo} alt="logo" onClick={goBack} />
                    </div>
                    <div className="search__form-input">
                        <form className="home__form" onSubmit={searchGoogle}>
                            <input
                                type="text"
                                className="home__input"
                                value={state}
                                onChange={(e) => setstate(e.target.value)}
                                required
                            />

                            <FaSistrix className="search__icon" />
                            <FaMicrophone className="microphone" />
                        </form>
                    </div>
                </div>
                <Show results={results} info={info} />
            </div>
        </>
    );
}

export default Search;
